import React, { useEffect, useState } from 'react';
import reactDom from 'react-dom';
import { useDropzone } from 'react-dropzone';
import styled from 'styled-components';
import uploadImage from '../../../Services/uploadImage';
import { colorGreen, colorRed, desktopMediaQuery } from '../../../styles';
import FileInputNew from './FileInputNew';

const BackgroundContainer = styled.div`
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999999999;
  display: flex;
  justify-content: center;
  background: #0008;
  backdrop-filter: blur(3px);

  ${desktopMediaQuery} {
    align-items: center;
  }
`;

const Container = styled.div`
  height: 80%;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 3vh;
  margin-top: 10%;
  background: #fff;
  border-radius: 5px;

  ${desktopMediaQuery} {
    width: 30%;
    height: 80%;
    margin-top: 0;
  }
`;

const Tittle = styled.h4`
  font-size: 2.3em;
  text-align: center;
`;

const FilesContainer = styled.div`
  height: 70%;
  width: 100%;
  overflow: auto;
`;

const FilesContainerActive = {
  background: '#55f4',
};

const FilesMessageContainer = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #666;
  font-size: 2em;
  border: 3px dashed #aaa;
`;

const ButtonsContainer = styled.div`
  height: fit-content;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  ${desktopMediaQuery} {
    flex-direction: row;
  }
`;

const ButtonsSubContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;

  ${desktopMediaQuery} {
    padding-left: 1.5em;
  }
`;

const Button = styled.button`
  height: fit-content;
  min-width: fit-content;
  /* font-size: ${({ status }) => (status === 'uploading' || status === 'uploaded' ? `1.1em` : `1.3em`)}; */
  font-size: 1.2em;
  padding: 8px 22px;
  margin-top: 10px;
  background: ${({ color }) => (color ? `${color}` : `transparent`)};
  border: 2px solid ${({ color }) => (color ? color : `#0005`)};
  border-radius: 5px;
  cursor: pointer;

  transition: filter 0.3s;

  &:hover {
    filter: brightness(90%);
  }

  ${desktopMediaQuery} {
    margin-top: 0;
  }
`;

const UploadFilesModal = ({ formProductData, setFormProductData, setOpenUploadFiles }) => {
  const [images, setImages] = useState(null);
  const [status, setStatus] = useState(null);

  const { isDragActive, getRootProps, getInputProps } = useDropzone({
    noClick: images === null ? false : true,
    onDrop: (acceptedFiles) => {
      if (images === null) setImages([...acceptedFiles]);
      else setImages([...images, ...acceptedFiles]);
    },
  });

  useEffect(() => {
    //en caso de que el array este vacio, establecerlo como null
    if (!images?.length) setImages(null);
  }, [images]);

  useEffect(() => {
    // const requests = formProductData.imagesURLs.map((el) => fetch(el));
    // Promise.all([...requests]).then((res) => {
    //   console.log(res);
    // });
    console.log(formProductData.imagesURLs[0]);
    // if (formProductData.imagesURLs) fetch().then((res) => console.log('Listo'));
    const test = new Image();
    test.src = formProductData.imagesURLs[0];
    console.log(test.height, test.width);

    // new Image().
    // formProductData.
    // fetch();
  }, [formProductData.imagesURLs]);

  const handleCancel = () => {
    setOpenUploadFiles(false);
  };

  const handleAddImage = (e) => {
    const newImages = e.target.files;
    if (images === null) setImages([...newImages]);
    else setImages([...images, ...newImages]);
  };

  /**********************/
  /**** ModifyImages ****/
  /**********************/

  const changeImageIndex = (elementToReposition, direction) => {
    let newIndex;
    let arrayToUpdate = images;

    //si es para arriba set el nuevo indice como uno menos del actual, en caso de ser para abajo hacer lo contrario
    if (direction === 'up') newIndex = images.indexOf(elementToReposition) - 1;
    if (direction === 'down') newIndex = images.indexOf(elementToReposition) + 1;

    //prevenir en caso de que el nuevo indice sea -1 y en caso de que el nuevo indice sea mayor al largo del aray
    if (newIndex === -1) newIndex = 0;
    if (newIndex === arrayToUpdate.length) newIndex = arrayToUpdate.length - 1;

    //elimina el elemento a reposicionar
    arrayToUpdate.splice(images.indexOf(elementToReposition), 1);

    //separa lo que va a ir antes y lo que va a ir despues del nuevo array
    let before = arrayToUpdate.slice(0, newIndex);
    let after = arrayToUpdate.slice(before.length, arrayToUpdate.length);

    //set del nuevo array
    setImages([...before, elementToReposition, ...after]);
  };

  const deleteImageIndex = (elementToDelete) => {
    let arrayToUpdate = images;

    //elimina el elemento selecionado
    arrayToUpdate.splice(images.indexOf(elementToDelete), 1);

    console.log(arrayToUpdate);

    setImages([...arrayToUpdate]);
  };

  /*****************/
  /**** Changes ****/
  /*****************/

  const handleUploadFiles = async () => {
    if (!images || images.length === 0) {
      alert('Necesitas elegir una imagen para subir');
      return false;
    }
    setStatus('uploading');

    //sube las imagenes y retorna las URLs y los paths
    const { imagesURLs, imagesPaths } = await uploadImage(formProductData, images);

    setFormProductData({ ...formProductData, imagesPaths, imagesURLs }); //guarda los paths y las URLs de las imgenes en el estado
    setStatus('uploaded');

    setTimeout(() => {
      setOpenUploadFiles(false);
    }, 1000);
  };

  return reactDom.createPortal(
    <BackgroundContainer>
      <Container>
        <Tittle>Upload a file</Tittle>
        <FilesContainer style={isDragActive ? FilesContainerActive : {}} {...getRootProps()}>
          <input {...getInputProps()} />
          {images ? (
            images.map((data) => {
              return (
                <FileInputNew
                  key={data.name}
                  data={data}
                  changeImageIndex={changeImageIndex}
                  deleteImageIndex={deleteImageIndex}
                />
              );
            })
          ) : (
            <FilesMessageContainer>
              <>
                <i className="fas fa-cloud-upload-alt"></i>
                <p style={{ textAlign: 'center' }}>{isDragActive ? 'Suelta la imagen' : 'Selecciona una imagen'}</p>
              </>
            </FilesMessageContainer>
          )}
        </FilesContainer>
        <ButtonsContainer>
          <Button color="#5e82ea" style={{ position: 'relative', overflow: 'hidden' }}>
            <input
              onChange={handleAddImage}
              type="file"
              style={{ position: 'absolute', fontSize: '4em', top: -4, left: -4, opacity: '0', cursor: 'pointer' }}
            />
            Subir
            <i className="fas fa-upload"></i>
          </Button>
          <ButtonsSubContainer>
            <Button onClick={handleCancel} color={colorRed}>
              Cancelar
            </Button>
            <Button onClick={handleUploadFiles} color={colorGreen} status={status}>
              {status === null && 'Guardar'}
              {status === 'uploading' && 'Subiendo...'}
              {status === 'uploaded' && 'Listo!'}
            </Button>
          </ButtonsSubContainer>
        </ButtonsContainer>
      </Container>
    </BackgroundContainer>,
    document.getElementById('upload-files-modal')
  );
};

export default UploadFilesModal;
