import { useContext } from 'react';
import reactDom from 'react-dom';
import styled from 'styled-components';
import { colorGreen, colorRed, desktopMediaQuery } from '../../../styles';
import { AdminFormContext } from '../../../Context/AdminFormContext';

const Background = styled.div`
  height: 100vh;
  width: 100%;
`;

const Container = styled.div`
  height: 100vh;
  width: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(5px) brightness(70%);
`;

const Modal = styled.div`
  width: 90%;
  height: 40vh;
  display: flex;
  flex-direction: column;
  background: #fff;
  border-radius: 10px;
  box-shadow: -5px 0px 10px #000a;
  padding: 2vh 2vh;

  ${desktopMediaQuery} {
    width: 40%;
    padding: 4vh 6vh;
  }
`;

const Text = styled.p`
  height: 70%;
  text-align: center;
  font-size: 2em;
`;

const ButtonsContainer = styled.div`
  height: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
`;

const Button = styled.button`
  font-size: 1.3em;
  padding: 10px 20px;
  border: transparent;
  border-radius: 5px;
  background: ${({ color }) => color};
  cursor: pointer;

  ${desktopMediaQuery} {
    font-size: 1.5em;
    padding: 20px 40px;
  }
`;

const QuestionModal = () => {
  const { dataQuestionModal, closeQuestionModal } = useContext(AdminFormContext);
  const { question, callback } = dataQuestionModal;

  const handleClick = async () => {
    await callback();
    closeQuestionModal();
  };
  return reactDom.createPortal(
    <Background>
      <Container>
        <Modal>
          <Text>{question}</Text>
          <ButtonsContainer>
            <Button color={colorRed} onClick={closeQuestionModal}>
              No
            </Button>
            <Button color={colorGreen} onClick={handleClick}>
              Si
            </Button>
          </ButtonsContainer>
        </Modal>
      </Container>
    </Background>,
    document.getElementById('modal')
  );
};

export default QuestionModal;
