import React, { useContext, useState } from 'react';
import styled from 'styled-components';
import { colorGreen, colorRed } from '../../../styles';
import { AdminFormContext } from '../../../Context/AdminFormContext';
import UploadFilesModal from './UploadFilesModal';
import { desktopMediaQuery } from '../../../styles';
import PreviewImagesModal from './PreviewImagesModal';

const FormStyle = styled.form`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* 
  ${desktopMediaQuery} {
    width: 50%;
  } */
`;

const GridContainer = styled.div`
  height: 70%;
  display: grid;
  grid-template-columns: 1fr 6fr;
  grid-auto-rows: repeat(5, 1fr);
  grid-row-gap: 3vh;
  align-items: center;
`;

const Tittle = styled.h4`
  display: flex;
  align-items: center;
  background: #0042;
  padding: 5px;
`;

const CategoryInput = styled.select`
  margin-left: 1em;
  background: transparent;
  border: none;
  font-size: 1.3em;
  cursor: pointer;
`;

const Label = styled.label`
  display: flex;
  align-items: center;
  font-size: 1.1em;
  padding: 0 10px;
  color: #000;
`;

const TextInput = styled.input`
  width: 80%;
  height: 5vh;
  font-size: 1.2em;
`;

const NumberInput = styled(TextInput)`
  width: 80%;
  font-size: 1.4em;
`;

const AreaInput = styled.textarea`
  width: 80%;
  height: 15vh;
  font-size: 1.2em;
`;

const SelectInput = styled.select`
  width: 80%;
  height: 5vh;
  font-size: 1.4em;
`;

const UploadImageButton = styled.button`
  height: 100%;
  width: fit-content;
  margin: auto 0;
  cursor: pointer;
`;

const PreviewImageButton = styled(UploadImageButton)`
  margin-left: 1em;
`;

const UploadImageIcon = styled.i`
  padding: 3vh;
  font-size: 2.5em;
`;

const TagsContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 10px 6%;
`;

const CheckContainer = styled.div`
  display: flex;
  align-items: center;
`;

const CheckInput = styled.input`
  height: 1.2em;
  width: 1.2em;
  margin-right: 5px;
  cursor: pointer;
`;

const ButtonsContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-around;
`;

const ButtonInput = styled.input`
  width: 45%;
  height: 6vh;
  background: ${({ color }) => (color ? color : `#f00`)};
  border: none;
  border-radius: 5px;
  font-size: 1.2em;
  cursor: pointer;
`;

const AdminForm = () => {
  const { adminCategory, setAdminCategory, closeForm, status, dataToFill, editProduct, addProduct } =
    useContext(AdminFormContext);
  const [openUploadFiles, setOpenUploadFiles] = useState(false);
  const [openPreviewFiles, setOpenPreviewFiles] = useState(false);
  const dataLayout = {
    id: '',
    name: '',
    category: adminCategory,
    description: '',
    price: '',
    imagesURLs: [],
    tags: [],
    outOfStock: false,
    order: 5,
    subOrder: Math.random(),
  };
  const [formProductData, setFormProductData] = useState(dataToFill || dataLayout);

  const handleInputChange = (e) => {
    let { name, value } = e.target;
    let idValue;

    //en caso de que el campo modificado sea el modelo, actualizar tambien el ID
    if (name === 'name') idValue = e.target.value.replace(/ /g, '-').toLowerCase();

    //set del formProductData con el campo y valor modificado y, en el caso de que idValue tenga valor, setea el value como tal, en casod e que no lo deja igual
    setFormProductData({ ...{ ...formProductData, [name]: value }, id: idValue ? idValue : formProductData.id });
  };

  const handleCheckboxChange = (e) => {
    //maneja el completado de datos para las checkboxes
    let { name, checked } = e.target;

    if (checked) {
      setFormProductData({ ...formProductData, tags: [...formProductData['tags'], name] });
    } else {
      setFormProductData({ ...formProductData, tags: [...formProductData['tags'].filter((el) => el !== name)] });
    }
  };

  const handleOutOfStockChange = (e) => {
    console.log(e.target.checked);
    setFormProductData({ ...formProductData, outOfStock: e.target.checked });
  };

  const handleUploadFilesClick = (e) => {
    e.preventDefault();
    setOpenUploadFiles(true);
  };

  const handlePreviewImagesClick = (e) => {
    e.preventDefault();
    setOpenPreviewFiles(true);
  };

  const handleCategoryChange = (e) => {
    setAdminCategory(e.target.value);
    setFormProductData({ ...formProductData, category: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (dataToFill) {
      await editProduct(formProductData);
    } else {
      await addProduct(formProductData);
    }
  };

  return (
    <>
      <FormStyle onSubmit={handleSubmit} action="">
        {/* ******* */}
        {/* PRODUCT */}
        {/* ******* */}
        <Tittle>
          Producto:
          <CategoryInput value={adminCategory} name="category" onChange={handleCategoryChange}>
            <option value="vinos">Vinos</option>
            <option value="vodka">Vodka</option>
            <option value="espumantes">Espumantes</option>
          </CategoryInput>
        </Tittle>
        <GridContainer>
          {/* MODEL */}
          <Label htmlFor="model">Model</Label>
          <TextInput value={formProductData.name} onChange={handleInputChange} type="text" name="name" />

          {/* DESCRIPTION */}
          <Label htmlFor="description">Description</Label>
          <AreaInput
            value={formProductData.description}
            onChange={handleInputChange}
            type="text-area"
            name="description"
          />

          {/* PRICE */}
          <Label htmlFor="price">Price</Label>
          <NumberInput value={formProductData.price} onChange={handleInputChange} type="number" name="price" />

          {/* IMGS */}
          <Label htmlFor="imgs">IMGS</Label>
          {/* EL input upload que sea un componente que cuando agregues las imagenes se muestre el progreso */}
          <div style={{ display: 'flex', height: '11vh', overflow: 'hidden' }}>
            <UploadImageButton onClick={handleUploadFilesClick}>
              <UploadImageIcon className="fas fa-cloud-upload-alt"></UploadImageIcon>
            </UploadImageButton>
            <PreviewImageButton onClick={handlePreviewImagesClick}>
              <UploadImageIcon className="fas fa-image"></UploadImageIcon>
            </PreviewImageButton>
          </div>

          {/* ORDER */}
          <Label htmlFor="order">Relevance</Label>
          <SelectInput value={formProductData.order} onChange={handleInputChange} name="order">
            <option value={5}>5</option>
            <option value={4}>4</option>
            <option value={3}>3</option>
            <option value={2}>2</option>
            <option value={1}>1</option>
          </SelectInput>
        </GridContainer>

        {/* **** */}
        {/* TAGS */}
        {/* **** */}
        <Tittle>Tags</Tittle>
        <TagsContainer>
          {/* S */}
          <CheckContainer>
            <CheckInput
              type="checkbox"
              checked={formProductData.tags.some((el) => el === 'S')}
              onChange={handleCheckboxChange}
              name="S"
            />
            <Label htmlFor="S">S</Label>
          </CheckContainer>

          {/* M */}
          <CheckContainer>
            <CheckInput
              type="checkbox"
              checked={formProductData.tags.some((el) => el === 'M')}
              onChange={handleCheckboxChange}
              name="M"
            />
            <Label htmlFor="M">M</Label>
          </CheckContainer>

          {/* L */}
          <CheckContainer>
            <CheckInput
              type="checkbox"
              checked={formProductData.tags.some((el) => el === 'L')}
              onChange={handleCheckboxChange}
              name="L"
            />
            <Label htmlFor="L">L</Label>
          </CheckContainer>

          {/* XL */}
          <CheckContainer>
            <CheckInput
              type="checkbox"
              checked={formProductData.tags.some((el) => el === 'XL')}
              onChange={handleCheckboxChange}
              name="XL"
            />
            <Label htmlFor="XL">XL</Label>
          </CheckContainer>

          {/* OUT OF STOCK */}
          <CheckContainer>
            <CheckInput
              type="checkbox"
              checked={formProductData.outOfStock}
              onChange={handleOutOfStockChange}
              name="XL"
            />
            <Label htmlFor="XL">Sin Stock</Label>
          </CheckContainer>
        </TagsContainer>

        <ButtonsContainer style={{ display: 'flex' }}>
          <ButtonInput type="button" value="Cancelar" color={colorRed} onClick={closeForm} />
          <ButtonInput
            type="submit"
            value={status === null ? 'Guardar' : status === 'uploading' ? 'Guardando...' : 'Guardado!'}
            color={colorGreen}
          />
        </ButtonsContainer>
      </FormStyle>
      {openUploadFiles && (
        <UploadFilesModal
          formProductData={formProductData}
          setFormProductData={setFormProductData}
          setOpenUploadFiles={setOpenUploadFiles}
        />
      )}

      {openPreviewFiles && (
        <PreviewImagesModal images={formProductData.imagesURLs} setOpenPreviewFiles={setOpenPreviewFiles} />
      )}
    </>
  );
};

export default AdminForm;
