import styled from 'styled-components';
import { desktopMediaQuery } from '../../styles';

const AmountContainer = styled.div`
  width: ${({ width }) => (width ? width : '40%')};
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 0.8em;

  ${desktopMediaQuery} {
    width: ${({ width }) => (width ? width : '30%')};
    /* width: 30%; */
  }
`;

const AmountButton = styled.button`
  height: 2.3em;
  width: 2.3em;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  background: #dd6b20;
  border: none;
  outline: none;
  border-radius: 100px;
  color: #fff;
  cursor: pointer;

  ${({ disabled }) => disabled && 'filter: grayscale(40%);'}
`;

const AmountValue = styled.p`
  font-size: 1.2em;

  ${desktopMediaQuery} {
    font-size: 1.4em;
  }
`;

const AmountCounter = ({ amount, incrementFunction, decrementFunction, width }) => {
  return (
    <AmountContainer width={width}>
      <AmountButton disabled={amount === 1 ? true : false} onClick={decrementFunction}>
        -
      </AmountButton>
      <AmountValue>{amount}</AmountValue>
      <AmountButton onClick={incrementFunction}>+</AmountButton>
    </AmountContainer>
  );
};

export default AmountCounter;
