import { useContext, useEffect } from 'react';
import styled from 'styled-components';
import AdminCardsContainer from '../../Components/Admin/Cards/AdminCardsContainer';
import { AdminFormContext } from '../../Context/AdminFormContext';
import AdminForm from '../../Components/Admin/Form/AdminForm';
import QuestionModal from '../../Components/Admin/Form/QuestionModal';
import { useHistory } from 'react-router';
import { desktopMediaQuery } from '../../styles';
import useAuth from '../../auth/useAuth';

const Container = styled.div`
  width: 100%;
  min-height: 100vh;
  height: fit-content;
  display: flex;
  flex-direction: column;
`;

const TopFormBar = styled.div`
  width: 98%;
  height: 10vh;
  position: relative;
  display: flex;
  align-items: center;
  margin: 0 auto;

  ${desktopMediaQuery} {
    width: 100%;
    padding: 0 6%;
  }
`;

const TopFormBarText = styled.p`
  font-size: 1.3em;
`;

const Button = styled.button`
  height: fit-content;
  width: fit-content;
  position: fixed;
  right: 1em;
  bottom: 1em;
  z-index: 9999;
  padding: 10px 20px;
  font-size: 1.1em;
  background: #fed610;
  box-shadow: 0px 5px 5px #0004;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  transition: all 0.3s;

  &:hover {
    filter: brightness(90%);
  }

  ${desktopMediaQuery} {
    position: absolute;
    right: 12vh;
    z-index: 1;
  }
`;

const CategorySelect = styled.select`
  width: fit-content;
  height: fit-content;
  margin-left: 10px;
  font-size: 1.5em;
  background: transparent;
  border: none;
  border-bottom: 2px solid #000;
  cursor: pointer;
  outline: none;
`;

const ProductsContainer = styled.div`
  width: 98%;
  margin: 0 auto;
`;

const AdminFormBakcground = styled.div`
  height: 130vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  background: #fff;

  ${desktopMediaQuery} {
    height: 100vh;
    padding-top: 4vh;
    background: #000a;
    backdrop-filter: blur(5px) brightness(70%);
    display: flex;
    justify-content: center;
    align-items: center;
  }
`;

const AdminFormContainer = styled.div`
  height: ${window.innerHeight}px;

  padding: 2vh 1vh;
  background: #fff;
  border-radius: 5px;

  ${desktopMediaQuery} {
    width: 40%;
    height: 90vh;
    padding: 3vh 3vh;
  }
`;

const ProductsAdminPage = () => {
  const { adminCategory, setAdminCategory, openAddProductModal } = useContext(AdminFormContext);
  const history = useHistory();
  const { setAdminMenu } = useAuth();

  useEffect(() => {
    setAdminMenu(true);
    window.scrollTo({ top: 0, left: 0, behavior: 'auto' });
  }, [setAdminMenu]);

  const handleCategoryChange = (e) => {
    setAdminCategory(e.target.value);
  };

  const historyHas = (direction) => {
    return history.location.pathname.split('/').some((el) => el === direction);
  };

  return (
    <Container>
      <TopFormBar>
        <TopFormBarText>Category: </TopFormBarText>
        <CategorySelect onChange={handleCategoryChange} value={adminCategory} name="category">
          <option value="vinos">Vinos</option>
          <option value="vodka">Vodka</option>
          <option value="espumantes">Espumantes</option>
        </CategorySelect>

        <Button onClick={openAddProductModal}>+ ADD PRODUCT</Button>
      </TopFormBar>

      <ProductsContainer>
        <AdminCardsContainer />
      </ProductsContainer>

      {historyHas('form') && (
        <AdminFormBakcground>
          <AdminFormContainer>
            <AdminForm />
          </AdminFormContainer>
        </AdminFormBakcground>
      )}

      {historyHas('question-modal') && <QuestionModal />}
    </Container>
  );
};

export default ProductsAdminPage;
