import { useContext } from 'react';
import styled from 'styled-components';
import { CartContext } from '../../Context/CartContext';
import AmountCounter from '../Utils/AmountCounter';
import trashImage from '../../Assets/Images/Icons/trash-solid.svg';
import { desktopMediaQuery } from '../../styles';
import formatPrice from '../../Services/formatPrice';

const CardContainer = styled.div`
  height: 18vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 1.5em;
  border-bottom: 1px solid #0003;
`;

const LeftContainer = styled.div`
  height: 100%;
  width: auto;
  display: flex;
  align-items: center;
`;

const ImageContainer = styled.div`
  height: 6.5em;
  width: 6.5em;
`;

const Image = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
`;

const ProductInfo = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 10px;
  padding-top: 1em;
  padding-bottom: 1em;
`;

const ProductName = styled.h4`
  font-size: 1.4em;
  font-weight: 500;
`;

const RightContainer = styled.div`
  height: 100%;
  width: auto;
  display: flex;
  justify-content: end;
  align-items: center;
`;

const Price = styled.p`
  font-size: 1.4em;
`;

const DeleteButton = styled.button`
  height: 0.8em;
  width: 0.8em;
  font-size: 1.5em;
  font-style: normal;
  margin-left: 0.5em;
  cursor: pointer;
  background: transparent;
  border: none;
  transform: translateY(-3px);
  transition: filter 0.3s;

  &:hover {
    filter: opacity(85%);
  }

  ${desktopMediaQuery} {
    height: 1em;
    width: 1em;
    margin-left: 1em;
    transform: translateY(1px);
  }
`;

const DeleteButtonImage = styled.img`
  object-fit: contain;
  height: 100%;
  width: 100%;
`;

const CartCard = ({ product, amount }) => {
  const { incrementOne, reduceOne, removeFromCart } = useContext(CartContext);

  const handleIncrementProductAmount = () => {
    incrementOne(product.id);
  };

  const handleReduceProductAmount = () => {
    if (amount === 1) return;
    else reduceOne(product.id);
  };

  const handleDeleteProduct = () => {
    removeFromCart(product.id);
  };

  return (
    <CardContainer>
      <LeftContainer>
        <ImageContainer>
          <Image src={product.imagesURLs[0]} />
        </ImageContainer>
        <ProductInfo>
          <ProductName>{product.name}</ProductName>
          <AmountCounter
            amount={amount}
            incrementFunction={handleIncrementProductAmount}
            decrementFunction={handleReduceProductAmount}
            width="70%"
          />
        </ProductInfo>
      </LeftContainer>
      <RightContainer>
        <Price>
          <strong>${formatPrice(product.price * amount)}</strong>
        </Price>

        <DeleteButton onClick={handleDeleteProduct}>
          <DeleteButtonImage src={trashImage} alt="" />
        </DeleteButton>
      </RightContainer>
    </CardContainer>
  );
};

export default CartCard;
